<template>
  <div id="JobChance">
    <div class="banner container-fuild text-center">工作机会</div>
    <div class="container">
      <div class="JobChance-container wow pulse">
        <h2>销售工程师</h2>
        <p>岗位职责</p>
        <ol>
          <li>1、 负责重点战略型客户的销售攻坚工作，负责新市场的销售拓展工作；</li>
          <li>2、 根据市场营销计划，进行区域内重点客户的需求挖掘和客情维护工作；</li>
          <li>3、 负责行业客户和竞争对手信息的收集和分析，提交Market Segment Report，为公司市场和研发人员提供相关数据的支持</li>
          <li>4、 负责按照公司要求定期提报Forecast，并为Forecast提供有力的数据支持；</li>
        </ol>
        <p>任职要求</p>
        <ol>
          <li>1、本科及以上学历，电子、微电子和光电等理工类相关专业毕业；</li>
          <li>2、一年以上电子行业相关销售经验，熟悉行业的销售模式和规则；</li>
          <li>3、具有较强自驱力及学习能力，热爱销售，具备较高的主动性和行动力，敢于并且乐于突破自己。</li>
        </ol>
        <button class="center-block btn btn-warning btn-lg" @click="gous">投递简历</button>
      </div>
    </div>
  </div>
</template>
<script>
import { WOW } from 'wowjs';
export default {
  name: "JobChance",
  data() {
    return {};
  },
  mounted(){
    var wow = new WOW();
    wow.init();
  },
  methods:{
    gous(){
      this.$router.push({
        path:"/contactus"
      })
    }
  }
};
</script>
<style scoped>
.banner {
  color: #fff;
  font-size: 30px;
  height: 150px;
  line-height: 150px;
  background-image: url("../assets/img/banner_2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  background-position: center center;
}
.JobChance-container {
  margin: 100px;
  padding: 30px;
  transition: all ease 0.5s;
  border: 1px dashed salmon;
}
.JobChance-container h2{
  color: rgb(255, 102, 19);
  font-weight: bolder;
  text-align: center;
}
.JobChance-container p{
font-size: 20px;
  color: rgb(255, 102, 19);
  font-weight: 700;
}
.JobChance-container ol{
    list-style-type: decimal;
    padding-left: 30px;
}
.JobChance-container ol>li{
    font-size: 14px;
    line-height: 2.7rem;
}
@media screen and (max-width: 997px) {
  .JobChance-container {
    margin: 20px 0;
    padding: 20px;
    border: 1px dashed salmon;
  }
}
</style>




